.eventDetails-root {
  width: 100%;
  height: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  background-color: #f7f8fa;

  .sup-navbar-center {
    font-size: 1rem;
  }

  .eventDetails-info-root {
    overflow: auto;
    width: 95%;
    background: #fff;
    position: relative;
    padding: 0.5rem;
    height: calc(100% - 30vmin);

    &>div {
      display: flex;
      justify-content: space-between;
      border-bottom: 1px solid #ccc;
      padding: 10px 0;

      &>div:nth-of-type(1) {
        min-width: 20%;
        color: rgb(149, 146, 146);
      }
    }
  }
  .root-footer-main {
    position: absolute;
    bottom: 0.5rem;
    width: 100%;
    display: flex;
    justify-content: space-around;
  }
}